<template>
    <div class="side">
        <div class="user-menu-shade" v-if="!isHidden" v-on:click="shadeClick">
        </div>
        <div class="user-menu" v-bind:class="{ hidden : isHidden}">
            <div class="user-menu-head">Game</div>
            <div class="menu-userinfo">
                <img src="../assets/user.jpeg">
                <div class="niki">超人</div>
                <div class ="user-item-info">
                     <div class="user-gold circle"></div><span>金币:1081</span>
                     <div class="user-flower"></div><span>鲜花:1</span>
                </div>
            </div>
            <div class="user-menu-list">
                <ul>
                    <li><span class="fa fa-comments-o"></span>交朋友<span class="fa fa-angle-right float-right"></span></li>
                    <li><span class="fa fa-gamepad"></span>玩游戏<span class="fa fa-angle-right float-right"></span></li>
                    <li><span class="fa fa-commenting-o"></span>意见反馈<span class="fa fa-angle-right float-right"></span></li>
                    <li><span class="user-gold circle"></span>签到领金币<span class="fa fa-angle-right float-right"></span></li>
                    <li><span class="fa fa-rocket"></span>关于Game<span class="fa fa-angle-right float-right"></span></li>
                </ul>
            </div>
        </div>
    </div>
</template>
<style>
    .user-menu-shade{
        height:100%;
        width:100%;
        background:#000000;
        opacity: 0.6;
        z-index:10001;
        position: fixed;
        top:0;
        left:0;
    }
    .user-menu{
        height:100%;
        width:70%;
        background:rgba(46,72,86,1);
        color:#cccccc;
        font-size:20px;
         z-index:10002;
        position:fixed;
        left:0;
        top:0;
        transition: left 0.5s;
        -moz-transition: left 0.5s;
        -webkit-transition: left 0.5s;
        -o-transition: left 0.5s;

    }
    .hidden{
        left:-100%;
    }
    .user-menu-head{
    font-size: 1.5em;
    text-align: center;
    color: #ffffff;
    border-bottom: 2px solid rgba(62, 91, 105, 1);
    }
    .menu-userinfo{
        margin-top:20px; 
        text-align: center;
    }
    .menu-userinfo img{
        height:120px;
        width: 120px;
        border-radius: 5px;
    }
    .user-menu-list{
        position: absolute;
        bottom: 5px;
        width:100%;
    }
    .user-menu-list li{
        display: block;
        width:100%;
        height:50px;
        background:rgba(62,91,105,1);
        margin:2px 0; 
        box-sizing: border-box;
        padding:12px 15px;
        
    }
    .user-menu-list span{
        margin-right:5px;
    }
    .user-gold{
        width:20px;
        height:20px;
        display: inline-block;
        background-image: url('../assets/gold.jpg');
        background-size:20px 20px;
        /* background-position:top left; */
    }
    .user-item-info{
        width:50%;
        margin: 0 auto;
        padding: 10px 0;
        position: relative;
    }
    .user-item-info span{
         position: relative;
         top:-3px;
         left:5px;
    }
    .circle{
        border-radius: 50%;
    }
    .float-right{
        float:right;

    }
</style>
<script type='text/ecmascript6'>
export default {
  name:'userMenu',
  props:['isHidden'],
  data(){
    return {
        isHide:true
    }
  },
  methods:{
      shadeClick(){
        this.$emit('transferEvent',this.isHide)
      }
  }
}
</script>





